import React, { useState, useEffect, useRef } from 'react';
import { createClient } from '@supabase/supabase-js';

function OptimisticApp() {
  const formRef = useRef();
  const scrollRef = useRef();
  const [newName, setNewName] = useState('');
  const [newCoinPrice, setNewCoinPrice] = useState('');
  const [newItemPrice, setNewItemPrice] = useState('');
  const [newPhotoPath, setNewPhotoPath] = useState('');
  const [updating, setUpdating] = useState(false);
  const [adding, setAdding] = useState(false);
  const [loading, setLoading] = useState(false);
  const [coinData, setCoinData] = useState(null);
  const [newCoinAmount, setNewCoinAmount] = useState('');
  const [sending, setSending] = useState(false);
  const [items, setItems] = useState([]); // State untuk menyimpan daftar item

  const supabaseUrl = 'https://sqgifjezpzxplyvrrtev.supabase.co';
  const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InNxZ2lmamV6cHp4cGx5dnJydGV2Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MTMzNDc2NzQsImV4cCI6MjAyODkyMzY3NH0.2yYEUffqta76luZ5mUF0pwgWNx3iEonvmxxr1KJge68';
  const supabase = createClient(supabaseUrl, supabaseKey);

  useEffect(() => {
    fetchCoinData();
    fetchItems(); // Panggil fungsi untuk mengambil daftar item saat komponen dimuat
  }, []);

  const fetchCoinData = async () => {
    try {
      setLoading(true);
      console.log('Fetching coin data...');
      const { data, error } = await supabase
        .from('coin')
        .select('harga, koin_tersedia')
        .single();
      if (error) throw error;
      setCoinData(data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching coin data:', error.message);
      setLoading(false);
    }
  };

  const fetchItems = async () => {
    try {
      setLoading(true);
      console.log('Fetching items...');
      const { data, error } = await supabase.from('item').select('*');
      if (error) throw error;
      setItems(data); // Set state dengan daftar item yang diambil dari database
      setLoading(false);
    } catch (error) {
      console.error('Error fetching items:', error.message);
      setLoading(false);
    }
  };

  const updatePriceAndAmount = async () => {
    try {
      setSending(true);
      console.log('Updating price and amount...');
      await supabase
        .from('coin')
        .update({ harga: newCoinPrice, koin_tersedia: newCoinAmount })
        .eq('id', 4);
      fetchCoinData();
      setSending(false);
    } catch (error) {
      console.error('Error updating price and amount:', error.message);
      setSending(false);
    }
  };

  const addItemToDatabase = async () => {
    try {
      setAdding(true);
      console.log('Adding item to database...');
      await supabase.from('item').insert([{ name: newName, price: newItemPrice, photopath: newPhotoPath }]);
      fetchItems(); // Perbarui daftar item setelah menambahkan item baru
      setAdding(false);
    } catch (error) {
      console.error('Error adding item to database:', error.message);
      setAdding(false);
    }
  };

  const deleteItemFromDatabase = async (itemId) => {
    try {
      setLoading(true);
      console.log('Deleting item from database...');
      await supabase.from('item').delete().eq('id', itemId);
      fetchItems(); // Perbarui daftar item setelah menghapus item
      setLoading(false);
    } catch (error) {
      console.error('Error deleting item from database:', error.message);
      setLoading(false);
    }
  };

  const handleNameChange = (e) => {
    setNewName(e.target.value);
  };

  const handlePriceChange = (e) => {
    setNewCoinPrice(e.target.value);
  };

  const handleItemPrice = (e) => {
    setNewItemPrice(e.target.value);
  };

  const handlePhotoPathChange = (e) => {
    setNewPhotoPath(e.target.value);
  };

  const handleAmountChange = (e) => {
    setNewCoinAmount(e.target.value);
  };

  return (
    <div className="app-container">
      <div className="console-container">
        <div className="console">
          <h3>Console</h3>
          <div className="console-content">
            {loading && <p>Loading...</p>}
            {coinData && (
              <div>
                <p>Current Price: {coinData.harga}</p>
                <p>Available Coins: {coinData.koin_tersedia}</p>
              </div>
            )}
            {sending && <p>Updating price and amount...</p>}
          </div>
        </div>
      </div>
      <div className="form-container">
        <input
          type="text"
          name="coin"
          placeholder={`Enter new price ${coinData ? `(${coinData.harga})` : ''}`}
          value={newCoinPrice}
          onChange={handlePriceChange}
        />
        <input
          type="text"
          placeholder={`Enter new coin amount ${coinData ? `(${coinData.koin_tersedia})` : ''}`}
          value={newCoinAmount}
          onChange={handleAmountChange}
        />
        <button className="button-85" onClick={updatePriceAndAmount} disabled={sending}>
          {sending ? 'Sending...' : 'Update Price and Amount'}
        </button>
      </div>
      <div className="form-container">
        <input
          type="text"
          name="item"
          placeholder="Enter item name"
          value={newName}
          onChange={handleNameChange}
        />
        <input
          type="text"
          placeholder={`Enter item price`}
          value={newItemPrice}
          onChange={handleItemPrice}
        />
        <input
          type="text"
          placeholder="Enter item photo path"
          value={newPhotoPath}
          onChange={handlePhotoPathChange}
        />
        <button className="button-85" onClick={addItemToDatabase} disabled={adding}>
          {adding ? 'Adding...' : 'Add Item to Database'}
        </button>
      </div>
      <div className="items-container">
        <h3>Items</h3>
        <ul>
          {items.map(item => (
            <li key={item.id}>
              <p>Name: {item.name}</p>
              <p>Price: {item.price}</p>
              <p>Photo Path: {item.photopath}</p>
              <button className='btn' onClick={() => deleteItemFromDatabase(item.id)}>Delete</button>
            </li>
          ))}
        </ul>
      </div>
      <div ref={scrollRef}></div>
    </div>
  );
}

export default OptimisticApp;
